import React, {useEffect, useMemo, useRef } from "react";
import '../../../css/admin/adminCs.css';
import {Col, Container, Row,Accordion } from "react-bootstrap";
import { useState } from "react";
import AdminAsideMenu from "../AdminAsideMenu";
import {Link, useNavigate} from "react-router-dom";
import ReactQuill from "react-quill";
import 'react-quill/dist/quill.snow.css';
import axios from "axios";

function Write() {
    const navigate = useNavigate();
    //  
    const [content, setContent] = useState("");
    const [title, setTitle] = useState("");
    const [registant, setRegistant] = useState('');

    const quillRef = useRef(null);

    //   
    const handleTitleChange = (e) => {
        setTitle(e.target.value);
    };

    //    
    useEffect(()=>{
        const email = localStorage.getItem('email');
        if (email) {
            setRegistant(email.slice(0, 3) + '***');
        }
    },[]);

    //  
    const handleImage = () => {

    }

    //  
    const modules = useMemo(() => {
        return {
            toolbar: {
                container: [
                    [{'header': [1, 2, 3, 4, 5, 6, false]}],
                    [{'font': []}],
                    [{'align': []}],
                    ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                    [{'list': 'ordered'}, {'list': 'bullet'}, 'link'],
                    [{'color': ['#000000', '#e60000', '#ff9900', '#ffff00', '#008a00', '#0066cc', '#9933ff', '#ffffff', '#facccc', '#ffebcc', '#ffffcc', '#cce8cc', '#cce0f5', '#ebd6ff', '#bbbbbb', '#f06666', '#ffc266', '#ffff66', '#66b966', '#66a3e0', '#c285ff', '#888888', '#a10000', '#b26b00', '#b2b200', '#006100', '#0047b2', '#6b24b2', '#444444', '#5c0000', '#663d00', '#666600', '#003700', '#002966', '#3d1466', 'custom-color']}, {'background': []}],
                    ['image', 'video'],
                    ['clean'],
                ],
                handlers: {
                    //     imageHandler   .
                    image: handleImage,
                },
            }, // toolbar
        }; // return
    }, []);


    const formats = [
        'header',
        'bold', 'italic', 'underline', 'strike', 'blockquote',
        'list', 'bullet',
        'link', 'image',
    ];

    return (
        <>
            <div>
                <form encType="multipart/form-data">
                    <h4>  <em>()</em></h4>
                    <input type="text" name="title" placeholder=" ." onChange={handleTitleChange}/>
                    <h4>  <em>()</em></h4>
                    <ReactQuill
                        style={{ width: "100%", height: "300px" }}
                        onChange={setContent}
                        modules={modules}
                        formats={formats}
                    />
                    <div className="links">
                        <Link to="/admin/notice/adminNoticeList" className="btnWrite noticeList"></Link>
                        <button type="button" className="btnWrite noticeReg"></button>
                    </div>
                </form>
            </div>
        </>
    );
}
function AdminNoticeWrite() {
    return (
        <section id="admin">
            <Container id="adminNoticeWrite">
                <Row className="justify-content-center">
                    {/* aside menu */}
                    <AdminAsideMenu/>
                    <Col className="write-content">
                        <div>
                            <h3> </h3>
                        </div>
                        <div>
                            <Write/>
                        </div>
                    </Col>
                </Row>
            </Container>
        </section>
    );
}

export default AdminNoticeWrite;